
import { Options, Vue } from 'vue-class-component';
import HelloWorld from '@/components/HelloWorld.vue';

@Options({
  components: {
    HelloWorld,
  },
  data() {
    return {
      tableData: []
    }
  },
  async created() {
    try {
      const {data} = await this.$http({
        url: '/test'
      })
      this.tableData = data.data
    } catch (e) {
      console.log(e)
    }
  },
})
export default class Home extends Vue {}
